
@import "../../scss/variables";

.perfect-datetimepicker {
    position: relative;
    width: 240px;
    margin: 2px 0 0;
    padding: 0;
    font-size: 14px;
    color: #333;
    background-color: #fafafa;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
    line-height: 0;
    overflow: hidden;
    border: 1px solid #ccc;
  
    * {
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
    }
  
    table {
      width: 100%;
      margin: 0;
      border: 0;
      border-collapse: separate;
      border-spacing: 2px;
      background-color: #fff;
      padding: 3px;
  
      td {
        border: 0;
        text-align: center;
        padding: 2px;
        width: 34px;
        height: 27px;
        cursor: pointer;
        vertical-align: middle;
        -webkit-border-radius: 3px;
        -moz-border-radius: 3px;
        border-radius: 3px;
  
        &.weekend {
          color: #d9534f;
        }
      }
  
      &.mt tbody td {
        width: 58px;
        height: 38px;
      }
    }
  
    td {
      &.day {
        width: 14.28571429%;
      }
  
      &.name {
        font-weight: 700;
      }
  
      &.oday {
        color: #ccc;
        cursor: auto;
      }
  
      &.disabled {
        color: #999;
        cursor: auto;
      }
  
      i {
        font-size: 18px;
        font-weight: 700;
      }
  
      table.tt {
        border: 0;
        padding: 0;
      }
    }
  
    table.tt {
      padding: 5px;
  
      td {
        &.hover {
          border: 0;
        }
  
        &.common {
          width: 5px;
        }
      }
  
      tbody td {
        width: 40px;
        height: 14px;
      }
  
      input {
        outline: 0;
        border: 1px solid #ccc;
        margin: 0 auto;
        padding: 0 !important;
        width: 38px;
        height: 24px;
        text-align: center;
        font-size: 12px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        vertical-align: middle;
        -webkit-border-radius: 3px;
        -moz-border-radius: 3px;
        border-radius: 3px;
        -moz-transition: border-color ease-in-out .2s,box-shadow ease-in-out .2s;
        -ms-transition: border-color ease-in-out .2s,box-shadow ease-in-out .2s;
        -webkit-transition: border-color ease-in-out .2s,box-shadow ease-in-out .2s;
        -o-transition: border-color ease-in-out .2s,box-shadow ease-in-out .2s;
        transition: border-color ease-in-out .2s,box-shadow ease-in-out .2s;
  
        &:focus {
          border-color: #66afe9;
          -webkit-box-shadow: 0 0 6px rgba(102, 175, 233, 0.6);
          -moz-box-shadow: 0 0 6px rgba(102, 175, 233, 0.6);
          box-shadow: 0 0 6px rgba(102, 175, 233, 0.6);
        }
  
        &[type=number] {
          &::-webkit-inner-spin-button, &::-webkit-outer-spin-button {
            -webkit-appearance: none;
            margin: 0;
          }
  
          -webkit-appearance: textfield;
          -moz-appearance: textfield;
          appearance: textfield;
        }
      }
    }
  
    .buttonpane {
      border-top: 1px solid #ccc;
    }
  
    tbody td {
      &.hover {
        background-color: #eee;
        padding: 1px;
        border: 1px solid #ccc;
      }
  
      &.today {
        font-weight: 700;
        color: #178cdf;
      }
  
      &.selected {
        font-weight: 700;
        padding: 1px;
        color: #fff;
        border: 1px solid #178cdf;
        background-color: #178cdf;
      }
    }
  
    tfoot td.hover {
      background-color: #eee;
      padding: 1px;
      border: 1px solid #eee;
    }
  
    thead td {
      &.hover {
        background-color: #eee;
        padding: 1px;
        border: 1px solid #eee;
      }
  
      &.title {
        font-weight: 700;
        text-align: center;
        padding: 2px;
      }
    }
  }
  
  i {
    font-family: finex;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  
    &.icon-datepicker-minus:before {
      content: '\e601';
    }
  
    &.icon-datepicker-plus:before {
      content: '\e603';
    }
  
    &.icon-datepicker-prev:before {
      content: '\e602';
    }
  
    &.icon-datepicker-next:before {
      content: '\e600';
    }
  }
  
  @font-face {
    font-family: finex;
    src: url(../resources/fonts/iconfont.eot);
    src: url(../resources/fonts/iconfont.eot?#iefix) format("embedded-opentype"), url(../resources/fonts/iconfont.woff) format("woff"), url(../resources/fonts/iconfont.ttf) format("truetype"), url(../resources/fonts/iconfont.svg#svgFontName) format("svg");
  }